@import "../../scss/Variables.scss";

// === FOOTER LINKS ===

.footer__links__container {
  @include flexSpaceBetween;
  margin: $base * 8 0 0 0;
  padding: 0;
  list-style: none;

  @media (max-width: $breakpoint-md-min) {
    flex-wrap: wrap;
    gap: $base * 5 0;
  }
}

.footer__links__container__item {
  @media (max-width: $breakpoint-md-min) {
    width: 50%;
  }
}

.footer__links__container__list {
  display: flex;
  flex-direction: column;
  gap: $base * 4;
  margin: 0;
  padding: 0;
  list-style: none;
}

.footer__links__container__list__header {
  font-size: $fs-m;
  font-weight: $fw-semi-bold;
  color: $color-dark;
  text-transform: uppercase;
}

.footer__links__container__list__item {
  display: flex;
  align-items: center;
  gap: $base * 2;
  font-size: $fs-xs;
  font-weight: $fw-regular;
  color: $color-dark-08;
}

.footer__links__container__list__item__link {
  color: $color-dark-08;
}

// === / FOOTER LINKS ===

// === FOOTER ===

.footer {
  margin-top: $base * 7;
  background-color: $color-gr-white;

  .inner__container {
    @include flexSpaceBetween;
    flex-wrap: wrap;
    margin-top: $base * 8;
    padding: $base * 2 0;

    @media (max-width: $breakpoint-md-min) {
      flex-direction: column;
      gap: $base * 4;
    }

    .footer__wrapper {
      width: 45%;
      filter: saturate(0);
    }

    .h3 {
      font-size: $fs-xxs;
      font-weight: $fw-medium;
      text-transform: none;
      color: $color-dark-08;
    }
  }

  .h3 {
    font-weight: $fw-medium;
    text-transform: none;
    color: $color-dark-08;

    a {
      color: $color-dark-08;
    }
  }
}

// === FOOTER ===
