@import "../../scss/Variables.scss";

// === CATALOG ===

.catalog__header {
  background-color: $color-gr-white;

  .h2 {
    font-size: 22px;
    font-weight: $fw-semi-bold;
    letter-spacing: 1px;
    text-transform: uppercase;
  }

  .catalog__filter {
    @include flexSpaceBetween;
    gap: 17px;
    margin: 0;
    padding: 0;
    list-style: none;
    text-transform: uppercase;
    font-size: $fs-xs;
    font-weight: $fw-regular;
    color: $color-dark-06;
    cursor: pointer;

    .catalog__filter__item {
      transition: $transition;

      &:hover {
        font-weight: $fw-regular;
        color: $color-dark;
        transition: $transition;
      }
    }
  }
}

.products__container {
  @include flexSpaceBetween;
  flex-wrap: wrap;
  gap: $base * 6 $base * 3;
  margin-top: 41px;
}

.related__products {
  position: relative;
  padding-bottom: 17px;
}

.product__card {
  margin: $base * 6 0 0 0;
  padding: 0;
  list-style: none;

  .product__card__image {
    display: block;
    width: $base * 51;
    height: $base * 64;
    margin: 0;
    padding: 0;
  }

  .product__card__title {
    margin-top: $base * 3;
    font-size: $fs-m;
    font-weight: $fw-regular;
    color: $color-dark-06;
  }

  .product__card__price__list {
    @include flexSpaceBetween;
    list-style: none;
    margin: 0;
    margin-top: 12px;
    padding: 0;

    .product__card__price__item {
      font-size: $fs-m;
      font-weight: $fw-bold;
      color: $color-dark;
    }
  }
}

// === / CATALOG ===

// === SLIDER BUTTONS ===

.swiper-button-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $base * 8;
  height: $base * 8;
  top: 44%;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: $color-white;
  background-image: url("./assets/img/ellipse__arrow__left.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &::after {
    content: "";
  }

  &.swiper-button-disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }

  @media (max-width: $breakpoint-md-min) {
    width: $base * 6;
    height: $base * 6;
  }
}

.swiper-button-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $base * 8;
  height: $base * 8;
  top: 44%;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: $color-white;
  background-image: url("./assets/img/ellipse__arrow__right.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &::after {
    content: "";
  }

  &.swiper-button-disabled {
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }

  @media (max-width: $breakpoint-md-min) {
    width: $base * 6;
    height: $base * 6;
  }
}

// === / SLIDER BUTTONS ===

.related__product__card {
  @media (max-width: $breakpoint-lg-min) {
    width: 100% !important;
  }
}
