@import "../../scss/Variables.scss";

// === BANNERS SECTION ===

.banners__section__container {
  @include flexSpaceBetween;
  margin: $base * 18 0 0 0;
  padding: 0;
  list-style: none;

  @media (max-width: $breakpoint-md-min) {
    flex-direction: column;
    gap: $base * 4;
  }
}

.banners__section__container__item {
  position: relative;
  @include flexCenter;
  width: 50%;

  @media (max-width: $breakpoint-md-min) {
    width: 100%;
  }

  .banner__img {
    width: 95%;
    height: auto;
  }
}

.banners__section__container__item__title {
  position: absolute;
  min-width: 50%;
  margin: 0;
  padding: 0;
  background-color: $color-white;
  text-align: center;
  border: none;
  border-radius: $base;
  transition: $transition;

  &:hover {
    min-width: 100%;
    transition: $transition;
  }

  @media (max-width: $breakpoint-xxs-min) {
    width: 60%;
  }

  .h3 {
    padding-top: $base * 5;
    font-size: $fs-xs;
    font-weight: $fw-semi-bold;
    letter-spacing: 1px;
    color: $color-dark-06;

    @media (max-width: $breakpoint-xxs-min) {
      padding: $base * 2 0 0 0;
    }
  }

  .h2 {
    padding: $base * 3 $base * 4;
    font-size: $fs-l;
    font-weight: $fw-semi-bold;
    letter-spacing: 1px;

    @media (max-width: $breakpoint-xxs-min) {
      padding: $base * 2 0;
    }

    .sale {
      color: $color-sale;
    }
  }
}

// === BANNERS SECTION ===
