@import "../../scss/Variables.scss";

// === SLIDER BANNERS ===

.banners__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: $base * 6;

  @media (max-width: $breakpoint-xs-min) {
    flex-direction: column;
  }

  .banners__container__right {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    width: 49%;

    .banners__container__right__gender {
      @include flexSpaceBetween;

      @media (max-width: $breakpoint-xs-min) {
        width: 204%;
        gap: $base * 2;
        margin-top: $base * 3;
      }
    }
  }

  .accessories {
    position: relative;
    @include flexCenter;
    width: 100%;

    @media (max-width: $breakpoint-xs-min) {
      width: 204%;
      margin-top: $base * 3;
    }

    .banner__img {
      position: relative;
      width: 100%;
      height: auto;
    }

    .slider__title {
      position: absolute;
      margin: 0;
      padding: 0;
      min-width: 50%;
      background-color: $color-white;
      text-align: center;
      border: none;
      border-radius: $base;
      transition: $transition;

      &:hover {
        min-width: 100%;
        transition: $transition;
      }

      .h2 {
        padding: $base * 3 $base * 4;
        font-size: $fs-m;
        font-weight: $fw-semi-bold;
        letter-spacing: 1px;
        line-height: $base * 4;

        @media (max-width: $breakpoint-md-min) {
          padding: $base $base;
        }
      }
    }
  }

  .women {
    position: relative;
    @include flexCenter;
    width: 48%;

    .banner__img {
      position: relative;
      width: 100%;
      height: auto;
    }

    .slider__title {
      position: absolute;
      margin: 0;
      padding: 0;
      min-width: 50%;
      background-color: $color-white;
      text-align: center;
      border: none;
      border-radius: $base;
      transition: $transition;

      &:hover {
        min-width: 100%;
        transition: $transition;
      }

      .h2 {
        padding: $base * 3 $base * 4;
        font-size: $fs-m;
        font-weight: $fw-semi-bold;
        letter-spacing: 1px;
        line-height: $base * 4;

        @media (max-width: $breakpoint-md-min) {
          padding: $base $base;
        }
      }
    }
  }

  .men {
    position: relative;
    @include flexCenter;
    width: 48%;

    .banner__img {
      position: relative;
      width: 100%;
      height: auto;
    }

    .slider__title {
      position: absolute;
      margin: 0;
      padding: 0;
      min-width: 50%;
      background-color: $color-white;
      text-align: center;
      border: none;
      border-radius: $base;
      transition: $transition;

      &:hover {
        min-width: 100%;
        transition: $transition;
      }

      .h2 {
        padding: $base * 3 $base * 4;
        font-size: $fs-m;
        font-weight: $fw-semi-bold;
        letter-spacing: 1px;
        line-height: $base * 4;

        @media (max-width: $breakpoint-md-min) {
          padding: $base $base;
        }
      }
    }
  }
}

// === / SLIDER BANNERS ===

// === SLIDER ===

.slider {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 49%;
  margin: 0 !important;
  overflow-x: hidden;

  @media (max-width: $breakpoint-xs-min) {
    width: 100%;
  }
}

.slider__img {
  width: 100%;
  height: auto;
}

.buttons__container {
  position: absolute;
  top: 38%;
  left: 2%;
  @include flexSpaceBetween;
  width: 96%;
  z-index: 2;

  @media (max-width: $breakpoint-md-min) {
    top: 33%;
  }

  @media (max-width: $breakpoint-sm-min) {
    top: 27%;
  }

  @media (max-width: $breakpoint-xs-min) {
    gap: $base * 3;
    top: 40%;
    left: 2%;
    width: 95%;
  }

  @media (max-width: $breakpoint-xxs-min) {
    top: 30%;
  }
}

.slider__arrow {
  @include flexCenter;
  min-width: $base * 8;
  min-height: $base * 8;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: $color-white;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  @media (max-width: $breakpoint-md-min) {
    min-width: $base * 6;
    min-height: $base * 6;
  }
}

.arrow__left {
  background-image: url("./assets/img/ellipse__arrow__left.svg");
  transition: $transition;

  &:hover {
    background-color: $color-grey;
    transition: $transition;
  }
}

.arrow__right {
  background-image: url("./assets/img/ellipse__arrow__right.svg");
  transition: $transition;

  &:hover {
    background-color: $color-grey;
    transition: $transition;
  }
}

.slider__title {
  margin: 0;
  padding: 0;
  background-color: $color-white;
  text-align: center;
  border: none;
  border-radius: $base;

  .h3 {
    padding-top: $base * 5;
    font-size: $fs-xs;
    font-weight: $fw-semi-bold;
    letter-spacing: 1px;
    line-height: $base * 3;
    color: $color-dark-04;

    @media (max-width: $breakpoint-md-min) {
      padding-top: $base;
    }
  }

  .h2 {
    padding: $base * 3 $base * 4;
    font-size: $fs-xl;
    font-weight: $fw-semi-bold;
    letter-spacing: 1px;
    line-height: $base * 5;

    @media (max-width: $breakpoint-md-min) {
      padding: $base $base;
    }
  }
}

.header {
  @media (max-width: $breakpoint-md-min) {
    width: 50%;
  }

  @media (max-width: $breakpoint-md-min) {
    top: 33%;
  }
}

// === / SLIDER ===

// === SLIDER BUTTONS ===

.swiper-button-prev {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $base * 8;
  height: $base * 8;
  top: 44%;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: $color-white;
  background-image: url("./assets/img/ellipse__arrow__left.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &::after {
    content: "";
  }

  @media (max-width: $breakpoint-md-min) {
    width: $base * 6;
    height: $base * 6;
  }
}

.swiper-button-next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $base * 8;
  height: $base * 8;
  top: 44%;
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 100%;
  background-color: $color-white;
  background-image: url("./assets/img/ellipse__arrow__right.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;

  &::after {
    content: "";
  }

  @media (max-width: $breakpoint-md-min) {
    width: $base * 6;
    height: $base * 6;
  }
}

// === / SLIDER BUTTONS ===
